#container{width:80px;height:80px;position:absolute;top:0%;left:0%}#burger{cursor:pointer;opacity:0;-webkit-animation:fadein 0.2s ease 1s forwards;animation:fadein 0.2s ease 1s forwards;overflow:hidden;position:relative;width:100%;height:100%}#burger .bun,#burger .filling{display:block;position:absolute;left:50%;transform:translate(-50%, -50%) rotate(0deg);height:10%;width:70%;transform-origin:50% 50%;transition:top 0.4s ease 0.4s, transform 0.4s ease 0.4s}#burger .bun:before,#burger .filling:before,#burger .bun:after,#burger .filling:after{content:"";display:block;height:40%;background:#d3d3d3;position:absolute;top:50%;transform:translate(0%, -45%);transition:background 0.2s ease, box-shadow 0.2s ease}#burger .bun:before,#burger .filling:before{left:0;width:calc(-1px + 75%);border-radius:10px 0px 0px 10px}#burger .bun:after,#burger .filling:after{right:0;width:calc(-1px + 25%)}#burger:hover .bun.top:before{background:#FEE;box-shadow:#F00 0px 0px 10px 1px}#burger:hover .bun.bottom:before{background:#DFF;box-shadow:#0FF 0px 0px 10px 1px}#burger:hover .filling:before{background:#EFE;box-shadow:#0F0 0px 0px 10px 1px}#burger .bun.top{top:25%}#burger .bun.bottom{top:75%}#burger .filling{top:50%;transform:translate(-50%, -50%) rotate(180deg);-webkit-animation:green-ls-out 0.8s ease forwards;animation:green-ls-out 0.8s ease forwards}#burger.active .bun{border-radius:3px;top:50%;transition:top 0.4s ease, transform 0.4s ease}#burger.active .bun.top{transform:translate(-50%, -50%) rotate(-225deg)}#burger.active .bun.bottom{transform:translate(-50%, -50%) rotate(405deg)}#burger.active .filling{transform:translate(-50%, -50%) rotate(-90deg);-webkit-animation:green-ls-in 0.8s ease forwards;animation:green-ls-in 0.8s ease forwards}.nav{font-family:'Audiowide', cursive;color:white;position:absolute;transform:translatex(-250%);transition:transform 0.2s ease;top:80px;bottom:0px;padding-right:16px}.nav.show{transform:translatex(0%)}.nav ul{list-style:none;padding:0;margin:0}.nav ul li{background:#ecfcff;box-shadow:#4df 0px 0px 0px 2px;font-size:1.50em;line-height:1.25em;padding:0px;padding-left:0px;margin:16px 0px;width:0;border-radius:0px 100px 100px 0px;transition:width 0.2s ease, padding 0.2s ease, color 0.2s ease, text-shadow 0.2s ease}.nav ul li:hover{box-shadow:#4df 0px 0px 20px 2px;text-shadow:#4df 0px 0px 6px,#4df 0px 0px 10px}.nav ul li:hover{width:100%;padding:0px 16px 0px 0px;color:#0e1111}.nav ul li a{display:block;text-decoration:none;width:375px;color:inherit;padding-left:16px}.nav ul li.green{background:#e6ffe6;box-shadow:lime 0px 0px 0px 2px}.nav ul li.green:hover{box-shadow:lime 0px 0px 20px 2px;text-shadow:lime 0px 0px 6px,lime 0px 0px 10px}.nav ul li.red{background:#ffe6e6;box-shadow:red 0px 0px 0px 2px}.nav ul li.red:hover{box-shadow:red 0px 0px 20px 2px;text-shadow:red 0px 0px 6px,red 0px 0px 10px}@-webkit-keyframes green-ls-in{0%{transform:translate(-50%, -50%) rotate(180deg)}50%{transform:translate(-50%, -50%) rotate(-90deg)}100%{transform:translate(-50%, -50%) rotate(-90deg) translate(200%, 0%)}}@keyframes green-ls-in{0%{transform:translate(-50%, -50%) rotate(180deg)}50%{transform:translate(-50%, -50%) rotate(-90deg)}100%{transform:translate(-50%, -50%) rotate(-90deg) translate(200%, 0%)}}@-webkit-keyframes green-ls-out{0%{transform:translate(-50%, -200%) rotate(-90deg)}50%{transform:translate(-50%, -50%) rotate(-90deg)}100%{transform:translate(-50%, -50%) rotate(180deg)}}@keyframes green-ls-out{0%{transform:translate(-50%, -200%) rotate(-90deg)}50%{transform:translate(-50%, -50%) rotate(-90deg)}100%{transform:translate(-50%, -50%) rotate(180deg)}}@-webkit-keyframes fadein{from{Opacity:0}to{Opacity:1}}@keyframes fadein{from{Opacity:0}to{Opacity:1}}

*{box-sizing:border-box;margin:0;padding:0}#root{font-family:'Nunito', sans-serif;min-width:350px}

